<template>
  <div class="seeCarList">
    <div class="seeCarHeader">
      <div class="seeCarHeader_content">
        <span class="return" @click="back"><img src="../../../assets/return.png" width="100%" alt=""></span>
        <span class="title">看车清单</span>
        <span class="editor" @click="showEditor">{{actionName}}</span>
        <span class="menu" @click="showMenu('看车单')"><img src="../../../assets/menu.png" width="100%" alt=""></span>
      </div>
      <div v-show="show_slide" class="slideDown">
        <div class="selectBtn">
          <div class="btnItem" @click="showAreaStatus" :class="{active:areaStatus}">
            <span class="allSignStatus">全部展区位置</span>
            <span>
              <template v-if="areaStatus">
                <i class="icon iconfont icon-jiankuohaoxishang"></i>
              </template>
              <template v-else>
                <i class="icon iconfont icon-jiankuohaoxixia"></i>
              </template>
            </span>
          </div>
          <div class="btnItem left" @click="showCarStatus" :class="{active:carStatus}">
            <span class="allCarStatus">全部车源状态</span>
            <span>
              <template v-if="carStatus">
                <i class="icon iconfont icon-jiankuohaoxishang"></i>
              </template>
              <template v-else>
                <i class="icon iconfont icon-jiankuohaoxixia"></i>
              </template>
            </span>
          </div>
        </div>
        <ul class="select_item" v-if="areaStatus">
          <li v-for="area in areas" @click="areaStateSelect(area)">
            <span>{{area.content}}区</span>
          </li>
        </ul>
        <ul class="select_item" v-if="carStatus">
          <li v-for="state in status" @click="statusSelect(state)">
            <span>{{state.content}}</span>
          </li>
        </ul>
      </div>
    </div>
    <div class="seeCarContent">
      <noSeen @changeCor="changeCor" @tab="toSeen" v-show="show_nose" ref="noSeen"></noSeen>
      <seen @changeCor="changeCor" v-show="show_seen" ref="seen"></seen>
    </div>
    <div class="scrollTop" @click="gotop" v-show="isScroll"></div>
    <div class="remark_toast"  v-show="showToast">
      <div class="toast">
        <p class="toast_content">您可以对已看的车辆进行已看标注及意向记录，以便您进行购车决策哦！</p>
        <p class="toast_btn" @click="closeToast">知道了</p>
      </div>
    </div>
    
    <div class="tab-btn">
      <p class="btn-nose active" @click="toNoSee">未看车辆</p>
      <p class="btn-seen" @click="toSeen">已看车辆</p>
    </div>
    <div v-show="delete_btn" class="delete">
      <button disabled="disabled" @click="deleteCar">删除</button>
    </div>
    <menuPage ref="menuMark" @close="closeThis"></menuPage>
  </div>
</template>
<script>
  import Vue from "vue";
  import menuPage from "../../public/nav.vue";
  import noSeen from "./noSeen/noSeen.vue";
  import seen from "./seen/seen.vue";
  import { Toast } from 'mint-ui';
  export default {
    data() {
      return {
        actionName: '编辑',
        delete_btn: false,
        show_slide: true,
        show_nose: true, //默认显示未看车单
        show_seen: false, //初始状态不显示已看车单
        noseNum: '',
        seenNum: '',
        s: [],
        localCity: '',
        showCirle: false,
        showToast: true,
        currentPage: 1,
        allPage: '',
        noCarImg: false,
        isScroll: false,
        showMenuPage: false,
        carStatus: false,
        areaStatus: false,
        evaluates: [{
            content: '车况良好'
          },
          {
            content: '保养记录好'
          },
          {
            content: '价格待谈'
          },
          {
            content: '性价比高'
          },
          {
            content: '内饰较新'
          },
          {
            content: '轻微剐蹭'
          },
          {
            content: '配置高'
          },
          {
            content: '购买意向大'
          },
        ],
        areas: [{
            content: '全部展'
          },
          {
            content: 'A'
          },
          {
            content: 'B'
          },
          {
            content: 'C'
          },
          {
            content: 'D'
          },
          {
            content: 'E'
          },
          {
            content: 'F'
          },
        ],
        status: [{
            content: '全部车源状态'
          },
          {
            content: '只看在售车辆'
          },
          {
            content: '只看已售出车辆'
          }
        ],
        commodityStatus: [], //车源状态筛选
        exhibitionArea: ''
      }
    },
    methods: {
      changeCor(num){
        if(num != 0){
          $(".delete button").attr("disabled",false);
        }else {
          $(".delete button").attr("disabled",true);
        }
      },

      toNoSee() { //切换未看车单

        $(".btn-nose").addClass("active");
        $(".btn-seen").removeClass("active");
        this.show_seen = false;
        this.show_nose = true;
        this.commodityStatus = [];
        this.exhibitionArea = "";
        this.$refs.noSeen.getWatchList(this.commodityStatus, this.exhibitionArea);
        document.getElementsByClassName("allSignStatus")[0].innerHTML = "全部展区位置";
        document.getElementsByClassName("allCarStatus")[0].innerHTML = "全部车源状态";

      },
      toSeen() { //切换已看车单

        $(".btn-nose").removeClass("active");
        $(".btn-seen").addClass("active");
        this.show_seen = true;
        this.show_nose = false;
        this.commodityStatus = [];
        this.exhibitionArea = "";
        this.$refs.seen.getWatchList(this.commodityStatus, this.exhibitionArea);
        document.getElementsByClassName("allSignStatus")[0].innerHTML = "全部展区位置";
        document.getElementsByClassName("allCarStatus")[0].innerHTML = "全部车源状态";

      },
      showEditor(){
        if(this.show_nose == true){
          if(this.actionName == "编辑"){
            this.$refs.noSeen.showSelect();
            this.actionName = '完成';
            this.delete_btn = true;
          }else {
            this.$refs.noSeen.closeSelect();
            this.actionName = '编辑';
            this.delete_btn = false;
          }
        }else {
          if(this.actionName == "编辑"){
            this.$refs.seen.showSelect();
            this.actionName = '完成';
            this.delete_btn = true;
          }else {
            this.$refs.seen.closeSelect();
            this.actionName = '编辑';
            this.delete_btn = false;
          }
        }
      },
      deleteCar(){
        if(this.show_nose == true){
          this.actionName = '编辑';
          this.delete_btn = false;
          this.$refs.noSeen.deleteList();
        }else{
          this.actionName = '编辑';
          this.delete_btn = false;
          this.$refs.seen.deleteListTwo();
        }
      },
      //    关闭toast层
      closeToast() {
        this.showToast = false;
        window.localStorage.setItem("knowTime", '已进入看车单');
      },
      //    返回顶部
      showTop() {
        var curHeight = document.documentElement.scrollTop || document.body.scrollTop;
        if(curHeight > 100) {
          this.isScroll = true
        } else {
          this.isScroll = false
        }
      },
      gotop() {
        $(window).scrollTop(0);
      },
      back() {
        this.$router.back(-1);
      },
      //    显示菜单
      showMenu(msg) {
        this.$refs.menuMark.show(msg, $(".seeCarList"));
      },
      //    控制当前页面显示隐藏
      closeThis() {
        this.showThis = false;
      },
      //    显示筛选状态
      showCarStatus() {
        this.carStatus = !this.carStatus
        this.areaStatus = false
      },
      showAreaStatus() {
        this.areaStatus = !this.areaStatus
        this.carStatus = false
      },
      statusSelect: function(state) { //车源状态筛选
        document.getElementsByClassName("allCarStatus")[0].innerHTML = state.content;
        var _this = this;
        if(state.content == '全部车源状态') {
          setTimeout(function() {
            _this.commodityStatus = [];
            if(_this.show_nose == true){
              _this.$refs.noSeen.getWatchList(_this.commodityStatus, _this.exhibitionArea);
            }else{
              _this.$refs.seen.getWatchList(_this.commodityStatus, _this.exhibitionArea);
            }
            _this.carStatus = false;
          }, 100)
        }
        if(state.content == '只看在售车辆') {
          setTimeout(function() {
            _this.commodityStatus = [2];
            if(_this.show_nose == true){
              _this.$refs.noSeen.getWatchList(_this.commodityStatus, _this.exhibitionArea);
            }else{
              _this.$refs.seen.getWatchList(_this.commodityStatus, _this.exhibitionArea);
            }
            _this.carStatus = false;
          }, 100)
        }
        if(state.content == '只看已售出车辆') {
          setTimeout(function() {
            _this.commodityStatus = [3, 4, 5, 6];
            if(_this.show_nose == true){
              _this.$refs.noSeen.getWatchList(_this.commodityStatus, _this.exhibitionArea);
            }else{
              _this.$refs.seen.getWatchList(_this.commodityStatus, _this.exhibitionArea);
            }
            _this.carStatus = false;
          }, 100)
        }
      },
      areaStateSelect: function(area) { //展区筛选
        document.getElementsByClassName("allSignStatus")[0].innerHTML = area.content + '区';
        var _this = this;
        if(area.content == '全部展') {
          setTimeout(function() {
            _this.exhibitionArea = '';
            _this.isLookOver = '';
            if(_this.show_nose == true){
              _this.$refs.noSeen.getWatchList(_this.commodityStatus, _this.exhibitionArea);
            }else{
              _this.$refs.seen.getWatchList(_this.commodityStatus, _this.exhibitionArea);
            }
            _this.areaStatus = false;
          }, 100)
        }else{
          setTimeout(function() {
            _this.exhibitionArea = area.content;
            _this.isLookOver = '';
            if(_this.show_nose == true){
              _this.$refs.noSeen.getWatchList(_this.commodityStatus, _this.exhibitionArea);
            }else{
              _this.$refs.seen.getWatchList(_this.commodityStatus, _this.exhibitionArea);
            }
            _this.areaStatus = false;
          }, 100)
        }
      }
    },
    mounted() {
      if(window.localStorage.getItem("knowTime")) {
        this.showToast = false;
      }
      this.tokens = JSON.parse(window.localStorage.getItem("user")).token;
      this.localCity = JSON.parse(window.localStorage.getItem("city"));
      window.addEventListener('scroll', this.showTop);
      var _this = this;
      $.ajax({
        type: "POST",
        url: this.global.host + 'watchList/list',
        data: {
          currentPage: 1,
          pagesize: 20,
          commodityStatus: JSON.stringify(_this.commodityStatus),
          location_city: _this.localCity,
          isLookOver: '',
          exhibitionArea: '',
          token: _this.tokens,
        },
        success: function(response) {
          if(response.resultCode == '10086' || response.resultCode == '1111' || response.resultCode == '911') {
            _this.show_slide = false;
          }
          if(response.resultMsg == "列表为空") {
              _this.show_slide = false;
          }else {
              _this.show_slide = true;
          }
        }  
      });
    },
    components: {
      menuPage,
      noSeen,
      seen
    }
  }
</script>
<style lang="scss" scoped>
  .seeCarList {
    position: absolute;
    background: #f5f5f5;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    overflow: auto;
    .seeCarHeader {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 3;
      width: 100%;
      max-width: 7.5rem;
      margin: 0 auto;
      background: #ffffff;
      .seeCarHeader_content {
        box-sizing: border-box;
        padding: .2rem .4rem;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        border-bottom: 1px solid #ececec;
        .return {
          width: 0.19rem;
          height: 0.34rem;
        }
        .title {
          font-size: 0.36rem;
          color: #000000;
        }
        .editor {
          position: absolute;
          right: .9rem;
          font-size: .28rem;
          top: .28rem;
          color: #000000;
        }
        .menu {
          width: 0.32rem;
          height: 0.32rem;
        }
      }
      .slideDown {
        width: 100%;
        background: #ffffff;
        height: auto;
        .selectBtn {
          background: #ffffff;
          border-bottom: 1px solid #ececec;
          display: flex;
          .btnItem {
            position: relative;
            width: 5rem;
            height: 0.8rem;
            display: flex;
            justify-content: center;
            align-items: center;
            &.active {
              color: #F6A32B;
            }
            span {
              font-size: 0.24rem;
            }
            .icon-jiankuohaoxishang {
              margin-left: .04rem;
              font-size: 0.28rem;
            }
            .icon-jiankuohaoxixia {
              margin-left: .04rem;
              font-size: 0.28rem;
            }
          }
        }
        .noCarImg {
          width: 100%;
          height: 100%;
          margin-top: 0.1rem;
          text-align: center;
          background: #ffffff;
          img {
            width: 4rem;
            height: 4.22rem;
            margin-top: 0.6rem;
          }
        }
        .select_item {
          position: absolute;
          z-index: 1;
          width: 100%;
          box-sizing: border-box;
          padding: 0 .4rem;
          background: #ffffff;
          li {
            box-sizing: border-box;
            padding: .3rem 0;
            width: 100%;
            border-bottom: 1px solid #f5f5f5;
            span {
              font-size: 0.28rem;
              color: #000000;
            }
          }
        }
      }
    }
    .seeCarContent {
      display: flex;
      overflow: hidden;
    }
    .scrollTop {
      position: fixed;
      bottom: 0.5rem;
      right: 0.2rem;
      border-radius: 2.5rem;
      width: 0.8rem;
      height: 0.8rem;
      background: url('./topIcon.png') no-repeat;
      background-size: 100%;
      z-index: 999;
      display: block;
    }
    .remark_toast {
      position: fixed;
      z-index: 3;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 100%;
      background: rgba(0, 0, 0, 0.3);
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      .toast {
        background: #ffffff;
        -webkit-border-radius: 0.2rem;
        -moz-border-radius: 0.2rem;
        border-radius: 0.2rem;
        width: 6rem;
        height: 2.33rem;
        display: flex;
        flex-direction: column;
      .toast_content {
        box-sizing: border-box;
        padding: .4rem .3rem;
        color: #666666;
        display: flex;
        align-items: center;
        font-size: .3rem;
        color: #000000;
        line-height: .48rem;
        border-bottom: 1px solid #ececec;
      }
      .toast_btn {
        color: #ff9600;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: .31rem;
        padding: .3rem 0;
        -webkit-border-radius: 0 0 0.2rem 0.2rem;
        -moz-border-radius: 0 0 0.2rem 0.2rem;
        border-radius: 0 0 0.2rem 0.2rem;
      }
    }
    }
    .tab-btn {
      position: fixed;
      bottom: .6rem;
      left: 50%;
      margin-left: -1.72rem;
      display: flex;
      background: #ffffff;
      box-shadow: 0 0 .3rem #999999;
      border-radius: .5rem;
      p {
        font-size: .3rem;
        width: 1.7rem;
        height: .7rem;
        text-align: center;
        line-height: .7rem;
        border-radius: .5rem;
        &.btn-nose.active {
          border: 1px solid #f6a32b;
          color: #f6a32b;
        }
        &.btn-seen.active {
          border: 1px solid #f6a32b;
          color: #f6a32b;
        }
      }
    }
    .delete {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      background: #ffffff;
      border-top: 1px solid #ececec;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      button {
        width: 6.04rem;
        height: .8rem;
        margin: .4rem 0;
        background: #ffb900;
        border-radius: .2rem;
        text-align: center;
        line-height: .8rem;
        font-size: .32rem;
        color: #ffffff;
        &[disabled = disabled] {
          color: #ffffff;
          background: #adadad;
        }
      }
    }
  }
  
  .mint-loadmore-top span {
    display: inline-block;
    vertical-align: middle
  }
  
  .mint-loadmore-bottom span {
    display: inline-block;
    vertical-align: middle
  }
</style>


