<template>
	<div class="seen">
		<mt-loadmore :top-method="loadTop" @top-status-change="handleTopChange" :bottom-method="loadMore" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore">
			<div class="noCarImg" v-if="noCarImg"><img src="../noCarLists@2x.png" alt=""></div>
			<ul class="carLists" v-else>
				<vue-loading class="loading" v-show="this.isLoading" type="spiningDubbles" color="#5ac1dd" :size="{ width: '1rem', height: '1rem' }"></vue-loading>
				<li class="carLists_item" v-for="(carList,index) in carLists">
					<p v-show="editor" class="selectTwo"><span @click="click_selectTwo(carList.vehicle_id,index)"></span></p>
					<div class="item-right">
						<dl>
							<router-link :to="{name: 'detail' , params:{id: carList.vehicle_id}}">
								<dt @click="saveHistory(carList,carList.vehicle_id)">
					                <img v-if="carList.thumbnail != null" :src="carList.thumbnail" alt="">
					                <img v-else src="../../../../assets/noImg.jpg" alt="">
					                <div class="remark" v-show="carList.commodity_status == 1||carList.commodity_status == 3||carList.commodity_status == 4||carList.commodity_status == 5||carList.commodity_status == 6">
					                  <p>已售出</p>
					                </div>
					            </dt>
							</router-link>
							<router-link class="carList-msg" :to="{name: 'detail' , params:{id: carList.vehicle_id}}">
								<dd>
									<p class="carMsgTitle">
										<span @click="saveHistory(carList,carList.vehicle_id)">{{carList.title}}</span>
									</p>
									<p @click="saveHistory(carList,carList.vehicle_id)">
										<span class="amount">{{carList.original_price}}万</span><span v-show="exhibition_area != null">/{{carList.exhibition_area}}区{{carList.exhibition_code}}</span>
									</p>
								</dd>
							</router-link>
						</dl>
						<div class="remarks" v-show="carList.is_look_over == true">
							<div class="tag-remark">
								<p>备注:</p>
								<ul>
					                <li v-for="remarkList in carList.remark">{{remarkList}}</li>
					            </ul>
							</div>
							<div class="user-remark">
								<p v-show="carList.customer_remark != ''">{{carList.customer_remark}}</p>
							</div>
						</div>
					</div>
				</li>
			</ul>
			<div slot="top" class="mint-loadmore-top">
				<span v-show="topStatus === 'loading'">
            <mt-spinner type="snake"></mt-spinner>
          </span>
			</div>
			<div slot="bottom" class="mint-loadmore-bottom">
				<span v-show="bottomStatus === 'loading'">
            <mt-spinner type="snake"></mt-spinner>
          </span>
			</div>
		</mt-loadmore>
	</div>
</template>
<style lang="scss" scoped>
