							box-sizing: border-box;
							padding: .2rem 0 .3rem 0.95rem;
							p {
								font-size: .28rem;
								color: #666666;
							}
						}	
					}
				}	
				
			}
		}
	}
	
	.scrollTop {
		position: fixed;
		bottom: 0.5rem;
		right: 0.2rem;
		border-radius: 2.5rem;
		width: 0.8rem;
		height: 0.8rem;
		background: url('../topIcon.png') no-repeat;
		background-size: 100%;
		z-index: 999;
		display: block;
	}
	
	.toast {
		position: absolute;
		background: #ffffff;
		-webkit-border-radius: 0.2rem;
		-moz-border-radius: 0.2rem;
		border-radius: 0.2rem;
		width: 5rem;
		height: 3rem;
		top: 40%;
		left: 1.25rem;
		display: flex;
		flex-direction: column;
		.toast_content {
			margin: 0 0.3rem;
			color: #666666;
			text-indent: 0.5rem;
			height: 2.2rem;
			display: flex;
