					            </dt>
								<dd>
									<p class="carMsgTitle">
										<span @click="saveHistory(carList,carList.vehicle_id)">{{carList.title}}</span>
									</p>
									<p @click="saveHistory(carList,carList.vehicle_id)">{{carList.first_register_date}}/{{carList.mileage}}万公里<span v-show="exhibition_area != null">/{{carList.exhibition_area}}区{{carList.exhibition_code}}</span></p>
									<p @click="saveHistory(carList,carList.vehicle_id)">
										<span class="amount">{{carList.original_price}}万</span>
										<span v-for="tag in carList.tags" class="tag" :class="{hand : tag == '练手', suv : tag == 'SUV', beNew : tag == '准新'}">{{tag}}</span>
									</p>
