          </span>
			</div>
		</mt-loadmore>

		<div id="remarks_window" v-show="ifShowRemarks" @touchmove.prevent>
			<div class="remarks_item">
				<div class="remarks_top">
					<p class="careInfo">备注该车源的看车意向，方便您最终评估决策：</p>
					<ul class="remarks">
						<li :id="'remarks_' + index" v-for="(evaluate,index) in evaluates" :class="{'remarks_active':evaluate.show}" @click="choose(evaluate)">{{evaluate.content}}</li>
					</ul>
					<div class="otherRemarks">
						<p>其他看车备注：</p>
						<textarea maxlength="50" rows="1" style="resize:none" placeholder="请输入您对该车源的意向备注" class="entry" name="textRemarks" id="textRemarks" cols="90"></textarea>
					</div>
				</div>
				<div class="btn">
