<script>
	import Vue from "vue";
	import vueLoading from 'vue-loading-template';
	import { Toast } from 'mint-ui';
	export default {
		data() {
			return {
				editor: false,
				isLoading: true,
				s: [],
				localCity: '',
				showCirle: false,
				showToast: true,
				currentPage: 1,
				knowTime: '',
				allPage: '',
				noCarImg: false,
				isScroll: false,
				ifShowRemarks: false,
				showMenuPage: false,
				area: false,
				carStatus: false,
				signStatus: false,
				curHeight: '',
				evaluates: [{
						content: '车况良好'
					},
					{
						content: '保养记录好'
					},
					{
						content: '价格待谈'
					},
					{
						content: '性价比高'
					},
					{
						content: '内饰较新'
					},
					{
						content: '轻微剐蹭'
					},
					{
						content: '配置高'
					},
					{
						content: '购买意向大'
					},
				],
				selectContent: [
					{
						content: '删除按钮'
					}
				],
				carLists: [],
				tokens: '',
				commodityStatus: [], //车源状态筛选
				isLookOver: '0', //标记状态筛选
				exhibitionArea: '',  //展区条件筛选
				signSee: '', //是否已看
				remarks: [],          //系统提供备注
      			customer_remark: '',  //用户自主评论
				nativeHref: '',
				topStatus: '',
				bottomStatus: '',
				allLoaded: false,
				carId: []
			}
		},
		methods: {
			showSelect(){
				this.editor = true;
				$(".noSeen").css("bottom","1.6rem");
			},
			closeSelect(){
				this.editor = false;
				$(".noSeen").css("bottom","0");
			},
			refresh(){
				this.getWatchList(this.commodityStatus, this.exhibitionArea);
			},
			loadTop() {
				setTimeout(() => {
					this.getWatchList(this.commodityStatus, this.exhibitionArea);
				}, 1500);
			},
			handleTopChange(status) {
				this.topStatus = status;
			},
			handleBottomChange(status) {
				this.bottomStatus = status;
			},
			// 加入历史浏览
			saveHistory(item, msg) {
				var Storage = window.localStorage;
				var ifHas = Storage.getItem("History");
				if(ifHas) {
					var newStorage = JSON.parse(ifHas);
					if(newStorage.length == 50) {
						newStorage.pop();
						newStorage.unshift(item);
						var arrTable = {},
							arrData = [];
						for(var i = 0; i < newStorage.length; i++) {
							if(!arrTable[newStorage[i].vehicle_id]) {
								arrTable[newStorage[i].vehicle_id] = true;
								arrData.push(newStorage[i]);
							}
						}
						Storage.setItem("History", JSON.stringify(arrData));
					} else {
						newStorage.unshift(item)
						var arrTable = {},
							arrData = [];
						for(var i = 0; i < newStorage.length; i++) {
							if(!arrTable[newStorage[i].vehicle_id]) {
								arrTable[newStorage[i].vehicle_id] = true;
								arrData.push(newStorage[i]);
							}
						}
						Storage.setItem("History", JSON.stringify(arrData));
					}
				} else {
					this.s.unshift(item)
					Storage.setItem("History", JSON.stringify(this.s));
				}
			},
			//    加载更多
			loadMore() {
				var _this = this;
				_this.currentPage += 1;
				setTimeout(function() {
					$.ajax({
						type: "POST",
						url: _this.global.host + "watchList/list",
						data: {
							currentPage: _this.currentPage,
							pagesize: 20,
							commodityStatus: JSON.stringify(_this.commodityStatus),
							customerId: _this.customerId,
							location_city: _this.localCity,
							isLookOver: 0,
							token: _this.tokens
						},
						success: function(response) {
							_this.$refs.loadmore.onBottomLoaded();
							if(response.resultCode === '0000') {
								if(_this.currentPage >= _this.allPage) {
									_this.allLoaded = true;
								}
								for(var i in response.result.list) {
									_this.carLists.push(response.result.list[i]);
								}
							} else if(response.resultCode === '1111') {
								alert(response.resultMsg);
							}
						}
					});
				}, 2000)
			},
			//    获取看车列表数据
			getWatchList(commodityStatus, exhibitionArea) {
				var _this = this;
				_this.commodityStatus = commodityStatus;
				_this.exhibitionArea = exhibitionArea;
				$.ajax({
					type: "POST",
					url: this.global.host + 'watchList/list',
					data: {
						currentPage: _this.currentPage,
						pagesize: 20,
						commodityStatus: JSON.stringify(commodityStatus),
						location_city: _this.localCity,
						isLookOver: 0,
						exhibitionArea: exhibitionArea,
						token: JSON.parse(window.localStorage.getItem("user")).token,
					},
					success: function(response) {
						_this.$refs.loadmore.onTopLoaded();
						_this.isLoading = false;
						if(response.resultCode == '10086') {
							_this.errorAjax(response.resultMsg, _this);
						}
						if(response.resultCode === '0000') {
							if(response.resultMsg == '列表为空') {
								_this.noCarImg = true;
								_this.showToast = false;
							} else {
								_this.carLists = response.result.list;
								_this.noCarImg = false;
								_this.allPage = response.result.pagination.pageCount;
								if(_this.carLists.length < 20) {
									_this.allLoaded = true;
								} else {
									_this.allLoaded = false;
								}
							}
						} else if(response.resultCode === '1111') {
							_this.showToast = false;
							_this.noCarImg = true;
						}
					},
					error: function(response) {

					}
				});
			},
			//    移除看车单
			deleteList: function() {
				var _this = this;
				$.ajax({
					type: "POST",
					url: this.global.host + 'watchList/remove',
					data: {
						token: JSON.parse(window.localStorage.getItem("user")).token,
						vehicleId: _this.carId.join(','),
					},
					success: function(response) {
						if(response.resultCode === '0000') {
							Toast("已移除看车单");
							_this.closeSelect();
							$(".select span").removeClass('active');
							_this.$emit("changeCor",0);
							_this.getWatchList(_this.commodityStatus,_this.exhibitionArea);
						} else if(response.resultCode === '1111') {
							Toast("操作失败");
						}
					}
				});
			},
			//    标为已看
			showRemarks(carList) {
				if(carList.is_look_over == true) {
					return;
				} else {
					this.ifShowRemarks = true;
					this.signSee = carList.vehicle_id;
				}
			},
			choose: function(evaluate) { //     选择评价
				if(evaluate.show == undefined) {
					Vue.set(evaluate, "show", false);
				}
				evaluate.show = !evaluate.show;
			},
			click_select(id,s){
				if($(".select span").eq(s).hasClass('active')){
					$(".select span").eq(s).removeClass('active');
					for(var i=0; i<this.carId.length; i++) {
					    if(this.carId[i] == id) {
						    this.carId.splice(i, 1);
					    }
					}
				}else{
					$(".select span").eq(s).addClass('active');
					this.carId.push(id);
				}
				this.$emit("changeCor",$(".select span.active").length);
			},
			cancel() { //      取消选择
				this.ifShowRemarks = false;
				$(".entry").val(''); //清空输入框
				$(".remarks li").removeClass("remarks_active") //取消选择

			},
			save() { //       确认选择
				for(var i = 0; i < $(".remarks_active").length; i++) {
					this.remarks.push($(".remarks_active")[i].innerHTML)
				}
				this.customer_remark = $(".entry").val();
				this.ifShowRemarks = false;
				this.updateRemark();
			},
			//    添加车辆评论
			updateRemark() {
				var _this = this;
				$.ajax({
					type: "POST",
					url: this.global.host + 'watchList/updateRemark',
					data: {
						token: JSON.parse(window.localStorage.getItem("user")).token,
						vehicleId: _this.signSee,
						customer_remark: _this.customer_remark,
						remark: JSON.stringify(_this.remarks),
						is_look_over: true
					},
					success: function(response) {
						if(response.resultCode === '0000') {
							_this.getWatchList(_this.commodityStatus, _this.exhibitionArea);
							$(".entry").val(''); //清空输入框
							$(".remarks li").removeClass("remarks_active") //取消选择
							_this.remarks = [];
							_this.$emit("tab","false");

						} else if(response.resultCode === '1111') {

						}
					}
				});
			},
			//    控制当前页面显示隐藏
			closeThis() {
				this.showThis = false;
			},
		},
		mounted() {
			this.tokens = JSON.parse(window.localStorage.getItem("user")).token;
			this.localCity = JSON.parse(window.localStorage.getItem("city"));
			this.getWatchList(this.commodityStatus, this.exhibitionArea);
		},
		components: {
			vueLoading
		}
	}
</script>


